.statistics .row {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    cursor: default;
}

.statistics .border-row {
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding-top: 0;
    padding-bottom: 0;
    cursor: default;
}

.statistics h6 {
    margin: 0;
}

.statistics .row:not(.border-row):nth-of-type(even) {
    background-color: rgba(0, 0, 0, 0.05);
}

.statistics .progress {
    height: 0.5rem;
}


