    background-size: cover;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 52px;
    width: 37px;
} */

tr img {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
}

.not-downloaded, .size-mismatch {
    background-image: -webkit-linear-gradient(135deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-image: -o-linear-gradient(135deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-image: linear-gradient(-45deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-size: 25px 25px;
}


